
import {
  IonCol,
  IonProgressBar,
  toastController,
  modalController,
} from "@ionic/vue";
import confrim from "@/components/confrim.vue";

export default {
  name: "Price",
  props: {
    percentage: Number,
    Aprice: Number,
    Rprice: Number,
    close: Boolean,
    buyer: String,
    Month: String,
    crop: String,
  },
  components: {
    IonCol,
    IonProgressBar,
  },
  methods: {
    async openModal(buyer, Month, Aprice, closed, crop) {
      const modal = await modalController.create({
        //@ts-ignore
        component: confrim,
        cssClass: "popup",
        componentProps: {
          buyer,
          Month,
          Aprice,
          closed,
          crop,
        },
      });
      return modal.present();
    },

    async openToast() {
      const toast = await toastController.create({
        message: "This window is closed.",
        duration: 2000,
        color: "dark",
        // translucent: true
      });
      return toast.present();
    },
    sell(buyer, Month, Aprice, closed, crop) {
      if (closed) {
        this.openToast();
        return;
      }
      console.log(Month);
      this.openModal(buyer, Month, Aprice, closed, crop);
    },
  },
};
