
import {
  IonCard,
  IonInput,
  IonLabel,
  IonItem,
  IonCardTitle,
  IonContent,
  IonButton,
  IonCol,
  IonRow,
  modalController,
  alertController,
  toastController,
} from "@ionic/vue";

import { defineComponent } from "vue";

export default defineComponent({
  name: "reg",
  data() {
    return {
      content: "Content",
    };
  },
  components: {
    IonCard,
    IonInput,
    IonLabel,
    IonItem,
    IonCardTitle,
    IonContent,
    IonButton,
    IonCol,
    IonRow,
  },
  created: function () {
    //   setTimeout(modalController.dismiss,1000)
  },
  methods: {
    async openToast() {
      const toast = await toastController.create({
        message:
          "registration success! Please wait for the verification email to be sent.",
        duration: 2000,
        color: "dark",
        // translucent: true
      });
      return toast.present();
    },

    register() {
      //sign up
      // @ts-ignore
      const username = document.getElementById("reusername").value;
      // @ts-ignore
      const psd1 = document.getElementById("repsw").value;
      // @ts-ignore
      const psd2 = document.getElementById("repsw2").value;

      if (psd1 != psd2) {
        alert("Password is not the same between two fileds.");
        return;
      }

      const myjson = {
        email: username,
        password: psd1,
        // @ts-ignore
        phone: document.getElementById("phone").value,
        pin: "string",
        // @ts-ignore
        farmername: document.getElementById("farmername").value,
        // @ts-ignore
        farmname: document.getElementById("farmname").value,
      };
      fetch("https://farmer.weathon.top/api/auth/register", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(myjson),
      }).then(async (response) => {
        if (response.ok) {
          // verify
          const toast = await toastController.create({
            message:
              "Registration success! Please wait for the verification email to be sent.",
            duration: 2000,
            color: "light",
            // translucent: true
          });
          toast.present() //wangjile weiosm cu await
          fetch("https://farmer.weathon.top/api/auth/request-verify-token", {
            method: "POST",
            headers: {
              "Content-Type": "application/json",
            },
            body: JSON.stringify({ email: username }),
          }).then((response) => {
            if (response.ok) {
              alert("Please check your email to verify.");
              modalController.dismiss();
            } else {
              alert("Error during request-verify-token");
            }
          });
        } else {
          alert("Error!");
        }
      });
    },
  },
});
