
import { IonSlides, IonSlide } from "@ionic/vue";
import { defineComponent } from "vue";
import Notifications from "@/views/Tab3.vue";
import buyer from "@/components/buyer.vue";
import Price from "@/components/Price.vue";
import myLogin from "@/views/Login.vue";
import { notificationsOutline } from "ionicons/icons";
import {
  IonPage,
  IonHeader,
  IonToolbar,
  IonTitle,
  IonContent,
  IonCol,
  // IonGrid,
  IonRow,
  IonItem,
  IonGrid,
  IonSegmentButton,
  IonSegment,
  IonLabel,
  IonMenuButton,
  modalController,
} from "@ionic/vue";
// import ExploreContainer from '@/components/ExploreContainer.vue';
export default defineComponent({
  name: "Tab1",
  components: {
    IonHeader,
    IonToolbar,
    IonTitle,
    IonContent,
    IonPage,
    IonCol,
    // IonGrid,
    IonItem,
    IonRow,
    IonSlides,
    IonSlide,
    buyer,
    Price,
    IonGrid,
    IonSegmentButton,
    IonSegment,
    IonLabel,
    IonMenuButton,
  },
  async mounted() {
    console.log("Mounted");
    this.myjson = await this.fetchMessages(this.crop);
    // console.log(this.myjson["Cargill CAMO"]["Jun"]["aprice"] / 100);
    let alreadylogin = false;
    await fetch("https://farmer.weathon.top/api/users/me", {
      method: "GET",
    }).then((response) => {
      if (response.ok) {
        alreadylogin = true;
      }
    });
    // jiancha?
    //     // if (success) modalController.dismiss();
    //     // else this.presentAlert();
    //   },
    // },
    console.log(alreadylogin)
    if (alreadylogin == false) {
      const modal = await modalController.create({
        component: myLogin,
        backdropDismiss: false,
      });
      alreadylogin = true;
      modal.present();
    }
    //@ts-ignore
    // setTimeout(modalController.dismiss,15000);
  },
  methods: {
    async fetchMessages(c) {
      console.log("Get data");
      // return []
      const response = await fetch(
        "https://farmer.weathon.top/api/price?crop=" + c,
        {
          method: "GET",
        }
      );
      this.loading = false;
      return response.json();
    },
    async segmentChanged1(ev) {
      console.log(ev.detail.value);
      this.crop = ev.detail.value;
      this.myjson = await this.fetchMessages(this.crop);
      console.log(this.myjson);
    },
    async openModal() {
      const modal = await modalController.create({
        component: myLogin,
        backdropDismiss: false,
      });
      return modal.present();
    },
    async openNotif() {
      const modal = await modalController.create({
        component: Notifications,
        backdropDismiss: false,
      });
      return modal.present();
    },
    switchGrain(id) {
      //@ts-ignore

      for (let i = 0; i < 4; i++) {
        //@ts-ignore
        document.getElementById("button" + i).setAttribute("enable", "false");
        // console.log(buttonId);
      } //反了？
      const buttonId = "button" + id.toString();
      //@ts-ignore
      document.getElementById(buttonId).setAttribute("enable", "true");
      // document.getElementById(buttonId).className += " enabled";
      console.log(buttonId);
    },
  },
  // async created() {

  // },
  data() {
    const crop = "Soybean";
    return { notificationsOutline, crop, loading: true, myjson: {} };
  },
});

