
import reg from "@/views/register.vue";

import {
  IonCard,
  IonInput,
  IonLabel,
  IonItem,
  IonCardTitle,
  IonContent,
  IonButton,
  IonCol,
  IonRow,
  modalController,
  alertController,
} from "@ionic/vue";

import { defineComponent } from "vue";

export default defineComponent({
  name: "myLogin",
  data() {
    return {
      content: "Content",
    };
  },
  components: {
    IonCard,
    IonInput,
    IonLabel,
    IonItem,
    IonCardTitle,
    IonContent,
    IonButton,
    IonCol,
    IonRow,
  },
  created: function () {
    //   setTimeout(modalController.dismiss,1000)
  },
  methods: {
    async presentAlert() {
      const alert = await alertController.create({
        header: "Password Error",
        message: 'Please retry or click "Reset Password"',
        buttons: ["OK"],
      });
      return alert.present();
    },
    async openreg() {
      const modal = await modalController.create({
        component: reg,
        backdropDismiss: false,
      });
      return modal.present();
    },
    login() {
      // @ts-ignore
      const username = document.getElementById("username").value;
      // @ts-ignore
      const psw = document.getElementById("psw").value;
      const fd = new FormData();
      fd.append("username", username);
      fd.append("password", psw);

      fetch("https://farmer.weathon.top/api/auth/jwt/login", {
        method: "POST",
        body: fd,
      }).then((response) => {
        if (response.ok) {
          modalController.dismiss();
        }
        else
        {
          this.presentAlert();
        }
      });

      // if (success) modalController.dismiss();
      // else this.presentAlert();
    },
  },
});
